.email {
  font-family: 'Inter', sans-serif;

  &__headline {
    margin: 0;
    @include bp(phone) {
      font-size: 48px;
      letter-spacing: -2.4px;
    }
  }

  &__copy {
    padding: 24px 0;

    & a {
      text-decoration: underline;
      color: $black;
    }
  }

  &__form {
    width: 100%;

    @include bp(phone) {
      width: 100%;
    }
  }
}

